<template>
    <div>
        <h1>Member List</h1>
        <div class="limiter">
            <div class="button-Container">
                <button class="button-56s buttonLeft" v-on:click="createMember">Create Member</button>
            </div>
            <div class="container-table100">
                <div class="wrap-table100">
                    <table id="data-table">
                        <thead>
                            <tr>
                                <th>Member ID</th>
                                <th>Chinese Name</th>
                                <th>English Name</th>
                                <th>Phone Number</th>
                                <th>Email</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="row in members" :key="row.id" @click="handleRowClick(row)">
                                <td>{{ row.memberID }}</td>
                                <td>{{ row.chineseName }}</td>
                                <td>{{ row.englishName }}</td>
                                <td>{{ row.telDaily }}</td>
                                <td>{{ row.email }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'MemberListPage',

    components: {

    },
    data() {
        return {
            chineseName: '',
            englishName: '',
            dob: '',
            hkid: '',
            telDaily: '',
            telNight: '',
            email: '',
            address: '',
            members: [], // Add members data property
        };
    },

    methods: {
        async ListAll() {
            this.members = []; // Clear the members array
            const response = await axios.get('http://localhost:3000/Person', {
                params: {
                    chineseName: this.chineseName,
                    englishName: this.englishName,
                    dob: this.dob,
                    hkid: this.hkid,
                    telDaily: this.telDaily,
                    telNight: this.telNight,
                    email: this.email,
                    address: this.address,
                },
            });
            this.members = response.data; // Store the fetched members data
        },
        createMember() {
            this.$router.push({ name: 'MemberCreatePage' });
        },
        handleRowClick(row) {
            console.log("Clicked row:", row);
            this.$router.push({ name: 'MemberInfoPage', query: { memberID: row.memberID } });
        },
    },

    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
        this.ListAll();
    },
};
</script>

