<template>

    <img class="logo" src="../../assets/logo0.png" alt="logo0">
    <h1>Login</h1>
    <br>

    <div class="login">
        <input type="text" v-model="username" placeholder="Enter Username: " required>
        <input type="password" v-model="password" placeholder="Enter Password: " required>
        <button class="button-56" v-on:click="validateAndSubmit">Log In</button>
    </div>

    <br>
    <p>Don't have an account? Go Register</p>
    <button class="button-56" v-on:click="goRegister">Register</button>

    <button class="button-56" v-on:click="goBack">Go Back</button>

</template>

<script>

import axios from 'axios';

export default {

    name: 'LoginPage',

    data() {
        return {
            username: null,
            password: null
        }
    },

    methods: {
        async validateAndSubmit() {
            // Perform field validation
            if (
                !this.username ||
                !this.password
            ) {
                alert("Please fill in all fields.");
                return;
            }
            this.submitLogin();
        },
        async submitLogin() {

            // DEBUG : CHECK INPUT
            console.log("[SYS-INFO]: " + "USERNAME-INPUT: " + this.username)
            console.log("[SYS-INFO]: " + "PASSWORD-INPUT: " + this.password)

            try {
                const response = await axios.get('http://localhost:3000/Users');
                const userList = response.data;

                // DEBUG: CHECK USER LIST
                console.log("[SYS-INFO]: USER-LIST: ", userList);

                // CHECK IF USERNAME AND PASSWORD MATCH IN DATABASE
                let user = await axios.get(`http://localhost:3000/Users?username=${this.username}`);

                if (user.data.length > 0 && user.status == 200) {

                    let result = await axios.get(`http://localhost:3000/Users?username=${this.username}&password=${this.password}`);

                    // DEBUG: CHECK RESULT
                    console.log("[SYS-INFO]: RESULT", result);

                    if (result.data.length > 0 && result.status == 200) {
                        alert('Login Successful!');

                        // DEBUG: LOGGED IN INFO
                        console.log("[SYS-INFO]: LOGGED IN INFO", result.data[0]);

                        // Store user info in local storage
                        localStorage.setItem('user-info', JSON.stringify(result.data[0]));
                        this.$router.push({ name: 'DashboardPage' });

                    } else {
                        alert('Invalid Password!');
                    }
                } else {
                    alert('Username does not exist!');
                }
            } catch (error) {
                console.error("An error occurred while fetching the user list:", error);
            }
        },

        goRegister() {
            this.$router.push({ name: 'RegisterPage' });
        },

        goBack() {
            this.$router.push({ name: 'LandingPage' });
        }
    },

    mounted() {
        if (localStorage.getItem('user-info')) {
            this.$router.push({ name: 'DashboardPage' });
        }
    }
}
</script>


<style>
.logo {
    width: 100px;
}

.login input {
    width: 300px;
    height: 40px;
    display: block;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
}
</style>