<template>
    <div>
        <h1>Case List</h1>
        <!-- <button class="button-56 buttonLeft" v-on:click="">Create Member</button> -->
        <div class="limiter">
            <div class="container-table100">
                <div class="wrap-table100">
                    <table id="data-table">
                        <thead>
                            <tr>
                                <th>Case ID</th>
                                <th>Member ID</th>
                                <th>Chinese Name</th>
                                <th>English Name</th>
                                <th>Phone Number</th>
                                <th>Email</th>

                            </tr>
                        </thead>
                        <tbody>
                            <!-- Table body will be dynamically generated here -->
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'CaseListPage',

    data() {
        return {
            caseID: '',
            englishName: '',
            chineseName: '',
            serialNo: '',
            cases: [], // Add case data property
        };
    },

    methods: {
        async ListAll() {
            this.cases = [];
            const memberResponse = await axios.get('http://localhost:3000/Person', {
                params: {

                    chineseName: this.chineseName,
                    englishName: this.englishName,
                },
            });
            const caseResponse = await axios.get('http://localhost:3000/Case', {
                params: {
                    caseID: this.caseID,
                    serialNo: this.serialNo,
                },
            });
            this.cases.push(memberResponse.data); // Store the fetched members data
            this.cases.push(caseResponse.data); // Store the fetched cases data
            this.generateTable(this.cases); // Generate the table
        },

        generateTable(data) {
            const tableBody = document.querySelector('#data-table tbody');
            tableBody.innerHTML = '';

            for (let user of data) {
                const row = document.createElement('tr');

                const caseIdCell = document.createElement('td');
                caseIdCell.textContent = user.caseId;
                row.appendChild(caseIdCell);

                const serialNoCell = document.createElement('td');
                serialNoCell.textContent = user.serialNo;
                row.appendChild(serialNoCell);

                const chineseNameCell = document.createElement('td');
                chineseNameCell.textContent = user.chineseName;
                row.appendChild(chineseNameCell);

                const englishNameCell = document.createElement('td');
                englishNameCell.textContent = user.englishName;
                row.appendChild(englishNameCell);

                tableBody.appendChild(row);

            }
        },
    },

    mounted() {
        this.ListAll();

        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }

    },
};
</script>

