<template>
    <div>
        <div v-if="treatmentInfo">
            <h2>Treatment Information</h2>
            <p><strong>Treatment Name:</strong> {{ treatmentInfo.treatmentName }}</p>
            <p><strong>Treatment ID:</strong> {{ treatmentInfo.treatmentID }}</p>
            <p><strong>Treatment type:</strong> {{ treatmentInfo.treatmentType }}</p>
            <p><strong>Treatment Fee:</strong> {{ treatmentInfo.treatmentFee }}</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'TreatmentInfoPage',

    data() {
        return {
            treatmentData: null,
        };
    },

    computed: {
        treatmentInfo() {
            return this.treatmentData && this.treatmentData.length > 0 ? this.treatmentData[0] : null;
        },
    },

    methods: {
        async getTreatmentInfo() {
            try {
                const treatmentID = this.$route.query.treatmentID;

                const response = await axios.get(`http://localhost:3000/Treatment?treatmentID=${treatmentID}`);
                this.treatmentData = response.data;
            } catch (error) {
                console.error('Error fetching treatment info:', error);
            }
        },
    },

    mounted() {
        this.getTreatmentInfo();

        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }

    },
};
</script>