<template>

  <router-view />

  <div id="app">
    <VueSidebarMenuAkahon :isUsedVueRouter="isUsedVueRouter" :isMenuOpen="true" :menuTitle="customMenuTitle"
      :menuLogo="customMenuLogoURL" :menu-icon="customMenuIcon" :menuItems="customMenuItems"
      :profileName="customProfileName" :profile-img="customProfileImg" :profileRole="customProfileRole"
      :isSearch="false" :isExitButton="true" :bgColor="navBackgroundColor" />
  </div>
</template>

<script>

import VueSidebarMenuAkahon from 'vue-sidebar-menu-akahon';


export default {
  name: 'App',
  components: {
    VueSidebarMenuAkahon
  },
  data() {
    return {
      customMenuItems: [
        {
          link: '/dashboard',
          name: 'Dashboard',
          tooltip: 'Dashboard',
          icon: 'bx-grid-alt',
        },
        {
          link: '/member',
          name: 'Member',
          tooltip: 'Member',
          icon: 'bx-user',
        },
        {
          link: '/case',
          name: 'Case',
          tooltip: 'Case',
          icon: 'bx-folder',
        },
        {
          link: '/activity',
          name: 'Activity',
          tooltip: 'Activity',
          icon: 'bx-pie-chart-alt-2',
        },
        {
          link: '/treatment',
          name: 'Treatment',
          tooltip: 'Treatment',
          icon: 'bx-capsule',
        },
        {
          link: '/dropin',
          name: 'Drop In',
          tooltip: 'Drop In',
          icon: 'bx-notepad',
        },
        {
          link: '/settings',
          name: 'Settings',
          tooltip: 'Settings',
          icon: 'bx-cog',
        },
      ],
      customMenuTitle: 'EldCare',
      // customMenuLogoURL: "./assets/logo0.png",
      customMenuIcon: 'bx-home-alt',
      customProfileName: 'Logout',
      customProfileImg: '',
      customProfileRole: '',
      navBackgroundColor: '#000000',
      isUsedVueRouter: false,
    };
  },
  created() {
    this.checkUserInfo();
  },
  methods: {
    checkUserInfo() {
      const userInfo = localStorage.getItem('user-info');
      if (!userInfo) {
        this.isUsedVueRouter = false;
      } else {
        this.isUsedVueRouter = true;
      }
    },
  },
  watch: {
    '$route'() {
      this.checkUserInfo();
    },
  },

}
</script>

