<template>

    <h1>Create Activity</h1>

    <div class="create-activity">

        <div class="row">
            <label for="productName">Activity Name:</label>
            <input type="text" v-model="productName" placeholder="Activity Name: ">

            <label for="quota">Quota:</label>
            <input type="text" v-model="quota" placeholder="Quota: ">

            <label for="fee">Fee:</label>
            <input type="text" v-model="fee" placeholder="Fee: ">
        </div>

        <div class="row">
            <label for="date">Date:</label>
            <input type="date" v-model="date" placeholder="Date: ">

            <label for="location">Location:</label>
            <input type="text" v-model="location" placeholder="Location: ">
        </div>

        <!-- <div class="row">
            <textarea v-model="sessions" name="sessions" rows="10" cols="100" placeholder="Sessions: ">
            </textarea>
        </div> -->

        <div class="row">
            <label for="remark">Remark:</label>
            <textarea v-model="remark" name="remark" rows="10" cols="100" placeholder="Remarks: ">
            </textarea>
        </div>

    </div>

    <button class="button-56" v-on:click="validateAndSubmit">Submit</button>

</template>

<script>

import axios from 'axios';
import ActivityMixin from '../../mixins/ActivityMixin.js';

export default {

    name: 'CreateActivityPage',
    mixins: [ActivityMixin],
    data() {
        return {
            productName: '',
            date: '',
            quota: '',
            fee: '',
            location: '',
            // sessions: '',
        }
    },

    methods: {
        async validateAndSubmit() {
            if (this.productName === '' || this.date === '' || this.quota === '' || this.fee === '' || this.location === '') {
                alert('Please fill in all fields');
                return;
            }
            this.submitCreate();
        },
        async submitCreate() {
            const productID = await this.generateProductID();

            const result = await axios.post('http://localhost:3000/Activity', {
                productName: this.productName,
                productID: productID,
                date: this.date,
                quota: this.quota,
                fee: this.fee,
                location: this.location,
                // sessions: this.sessions,
            });


            console.log(result);

            if (result.status === 201) {

                alert('Activity created successfully!');
                this.$router.push({ name: 'ActivityListPage' });
            }
        },
        async generateProductID() {
            // DEBUG
            console.log("GENERATING PRODUCT ID")
            console.log("ACTIVITY NUMBER-BEFORE: ", this.lastActivityNumber);

            this.incrementNumber();

            // DEBUG
            console.log('ACTIVITY NUMBER-UPDATED:', this.lastActivityNumber);


            const productId = this.lastActivityNumber;
            const currentYear = new Date().getFullYear();
            const formattedProductNumber = productId.toString().padStart(3, '0');

            // DEBUG
            console.log("ACTIVITY-YEAR", currentYear)
            console.log("ACTIVITY-NUMBER", productId)

            return `P${currentYear}${formattedProductNumber}`;

        },
    },

    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
    }

}
</script>


