<template>
    <div>
        <h1>Activity List</h1>

        <div class="limiter">
            <div class="button-Container">
                <button class="button-56s buttonLeft" v-on:click="createActivity">Create Activity</button>
                <button class="button-56s buttonLeft" v-on:click="goCalendarActivity">Activity Calendar</button>
                <button class="button-56s buttonLeft" v-on:click="goSignUpActivity">Sign Up</button>
            </div>

            <div class="container-table100">

                <div class="wrap-table100">
                    <table id="data-table">
                        <thead>
                            <tr>
                                <th>Activity ID</th>
                                <th>Name</th>
                                <th>Date</th>
                                <th>Quota</th>
                                <th>Fee</th>
                                <th>Location</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="row in activity" :key="row.id" @click="handleRowClick(row)">
                                <td>{{ row.productID }}</td>
                                <td>{{ row.productName }}</td>
                                <td>{{ row.date }}</td>
                                <td>{{ row.quota }}</td>
                                <td>{{ row.fee }}</td>
                                <td>{{ row.location }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'ActivityListPage',

    data() {
        return {
            productID: '',
            productName: '',
            date: "",
            quota: '',
            fee: '',
            location: "",
            sessions: [],
            activity: [], // Add case data property
        };
    },

    methods: {
        async ListAll() {
            this.activity = [];
            const response = await axios.get('http://localhost:3000/Activity', {
                params: {
                    productID: this.productID,
                    productName: this.productName,
                    date: this.date,
                    quota: this.quota,
                    fee: this.fee,
                    location: this.location,
                },
            });
            this.activity = response.data; // Store the fetched members data
        },
        createActivity() {
            this.$router.push({ name: 'ActivityCreatePage' });
        },

        // When the row of activity is clicked, pass the productID to the ActivityInfoPage
        handleRowClick(row) {
            console.log("Clicked row:", row);
            this.$router.push({ name: 'ActivityInfoPage', query: { productID: row.productID } });
        },

        // Redirections
        goCalendarActivity() {
            this.$router.push({ name: 'ActivityCalendarPage' });
        },
        goSignUpActivity() {
            this.$router.push({ name: 'ActivitySignUpPage' });
        },

    },
    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
        this.ListAll();
    },
};
</script>

<style>
@import url('../../css/table.css');
@import url('../../css/button.css');
@import url('../../css/index.css');
</style>