<template>
    <div>
        <h1>Treatment List</h1>

        <div class="limiter">
            <div class="button-Container">
                <button class="button-56s buttonLeft" v-on:click="createTreatment">Create Treatment</button>
                <button class="button-56s buttonLeft" v-on:click="bookTreatment">Book Treatment</button>
                <button class="button-56s buttonLeft" v-on:click="roasterTreatment">Staff Roaster</button>
            </div>

            <div class="container-table100">
                <div class="wrap-table100">
                    <table id="data-table">
                        <thead>
                            <tr>
                                <th>Treatment ID</th>
                                <th>Name</th>
                                <th>Type</th>
                                <th>Fee</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="row in treatments" :key="row.id" @click="handleRowClick(row)">
                                <td>{{ row.treatmentID }}</td>
                                <td>{{ row.treatmentName }}</td>
                                <td>{{ row.type }}</td>
                                <td>{{ row.treatmentFee }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'TreatmentListPage',

    data() {
        return {
            treatmentName: '',
            treatmentID: '',
            type: '',
            treatmentFee: '',
            treatments: [], // Add treatment data property
        };
    },

    methods: {
        async ListAll() {
            this.treatments = [];
            const response = await axios.get('http://localhost:3000/Treatment', {
                params: {
                    treatmentName: this.treatmentName,
                    treatmentID: this.treatmentID,
                    type: this.type,
                    treatmentFee: this.treatmentFee,
                },
            });
            this.treatments = response.data; // Store the fetched members data
        },
        createTreatment() {
            this.$router.push({ name: 'TreatmentCreatePage' });
        },
        handleRowClick(row) {
            console.log("Clicked row:", row);
            this.$router.push({ name: 'TreatmentInfoPage', query: { treatmentID: row.treatmentID } });
        },
        bookTreatment() {
            this.$router.push({ name: 'TreatmentBookingPage' });
        },
        roasterTreatment() {
            this.$router.push({ name: 'TreatmentRoasterPage' });
        }
    },

    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
        this.ListAll();
    },
};
</script>

<style>
@import url('../../css/table.css');
@import url('../../css/button.css');
@import url('../../css/index.css');
</style>