// MemberMixin.js

export default {
    data() {
        return {
            lastMemberNumber: 0,
        };
    },
    methods: {
        incrementMemberNumber() {
            this.lastMemberNumber++;
            localStorage.setItem('lastMemberNumber', this.lastMemberNumber);
        },
        // Other methods and properties...
    },
    created() {
        const storedLastMemberNumber = localStorage.getItem('lastMemberNumber');
        if (storedLastMemberNumber) {
            this.lastMemberNumber = parseInt(storedLastMemberNumber);
        }
    },
};