    <img class="logo" src="../assets/logo0.png" alt="logo0">
    <h1>Welcome to EldCare</h1>

    <div class="landing">
        <button class="button-56" v-on:click="goLogin">Login</button>
        <button class="button-56" v-on:click="goRegister">Register</button>
    </div>

</template>

<script>

export default {
    name: 'LandingPage',

    methods: {
        goLogin() {
            this.$router.push({ name: 'LoginPage' });

        },
        goRegister() {
            this.$router.push({ name: 'RegisterPage' });
        }
    },
    mounted() {
        if (localStorage.getItem('user-info')) {
            this.$router.push({ name: 'DashboardPage' });
        }
    },

}
</script>


