export default {
    data() {
        return {
            lastActivityNumber: 0,
        };
    },
    methods: {
        incrementNumber() {
            this.lastActivityNumber++;
            localStorage.setItem('lastActivityNumber', this.lastActivityNumber);
        },
    },
    created() {
        const storedLastActivityNumber = localStorage.getItem('lastActivityNumber');
        if (storedLastActivityNumber) {
            this.lastActivityNumber = parseInt(storedLastActivityNumber);
        }
    },
};