<template>
    <div>
        <h1>Create Member Page</h1>

        <div class="create-member">
            <div class="row">
                <label for="chineseName">Chinese Name:</label>
                <input v-model="chineseName" type="text" id="chineseName" required>

                <label for="englishName">English Name:</label>
                <input v-model="englishName" type="text" id="englishName" required>
            </div>
            <div class="row">
                <label for="dob">Date of Birth:</label>
                <input v-model="dob" type="date" id="dob" required>

                <label for="hkid">HKID:</label>
                <input v-model="hkid" type="text" id="hkid" required>
            </div>
            <div class="row">
                <label for="telDaily">Telephone (Daily):</label>
                <input v-model="telDaily" type="text" id="telDaily" required>

                <label for="email">Email:</label>
                <input v-model="email" type="email" id="email" required>
            </div>
            <div class="row">
                <label for="address">Address:</label>
                <textarea rows="4" cols="120" v-model="address" id="address" required></textarea>
            </div>
        </div>
        <button class="button-56" v-on:click="validateAndSubmit">Submit</button>

    </div>


</template>

<script>

import axios from 'axios';
import MemberMixin from '../../mixins/MemberMixin.js';

export default {

    name: 'CreateMemberPage',
    mixins: [MemberMixin],
    data() {
        return {
            chineseName: '',
            englishName: '',
            dob: '',
            hkid: '',
            telDaily: '',
            email: '',
            address: '',
        }
    },

    methods: {
        async validateAndSubmit() {
            // Perform field validation
            if (
                !this.chineseName ||
                !this.englishName ||
                !this.dob ||
                !this.hkid ||
                !this.telDaily ||
                !this.email ||
                !this.address
            ) {
                alert("Please fill in all fields.");
                return;
            }
            this.submitCreate();
        },
        async submitCreate() {
            try {
                const memberID = await this.generateMemberID(); // Generate member ID

                const result = await axios.post('http://localhost:3000/Person', {
                    memberID: memberID,
                    chineseName: this.chineseName,
                    englishName: this.englishName,
                    dob: this.dob,
                    hkid: this.hkid,
                    telDaily: this.telDaily,
                    email: this.email,
                    address: this.address,
                });

                console.log(result);

                if (result.status === 201) {
                    alert('Member created successfully!');
                    this.$router.push({ name: 'MemberListPage' });
                }
            } catch (error) {
                console.error('Error creating member:', error);
            }
        },
        async generateMemberID() {

            // DEBUG
            console.log("GENERATING MEMBER ID")

            console.log("Member Number: ", this.lastMemberNumber);
            this.incrementMemberNumber(); // Increment the member number
            console.log('Member Number:', this.lastMemberNumber);

            const memberNumber = this.lastMemberNumber;


            // await axios.put('http://localhost:3000/SysNextId', { lastMemberNumber: memberNumber });

            const currentYear = new Date().getFullYear();

            const formattedMemberNumber = memberNumber.toString().padStart(3, '0');

            // DEBUG
            console.log("MEMBER-YEAR", currentYear)
            console.log("MEMBER-NUMBER", memberNumber)

            return `M${currentYear}${formattedMemberNumber}`;
        }


    },
    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
    }

}
</script>


