<template>
    <div>
        <h1>Settings</h1>

        <div class="limiter">
            <div class="button-Container">
                <button class="button-56s buttonLeft" v-on:click="goUserSettings">User Settings</button>
                <button class="button-56s buttonLeft" v-on:click="goAccountSettings">Account Settings</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'SettingsPage',
    data() {
        return
    },
    methods: {
        goUserSettings() {
            this.$router.push({ name: 'UserSettingsPage' });
        },
        goAccountSettings() {
            this.$router.push({ name: 'AccountSettingsPage' });
        }
    },
    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
    }
}
</script>

