<template>

    <img class="logo" src="../../assets/logo0.png" alt="logo0">
    <h1>Register</h1>
    <br>

    <div class="register">
        <input type="text" v-model="username" placeholder="Enter Username: ">
        <input type="text" v-model="email" placeholder="Enter Email: ">
        <input type="password" v-model="password" placeholder="Enter Password: ">
        <button class="button-56" v-on:click="submitRegister">Submit</button>

        <br>
        <p>Already have an account? Go Login</p>
        <button class="button-56" v-on:click="goLogin">Login</button>
        <button class="button-56" v-on:click="goBack">Go Back</button>
    </div>

</template>

<script>

import axios from 'axios';

export default {

    name: 'RegisterPage',

    data() {
        return {
            username: '',
            email: '',
            password: '',
            status: '1' // default status to 1 == ACTIVE, 0 == SUSPEND
        }
    },

    methods: {
        async checkUserExist() {
            let userExist = await axios.get(`http://localhost:3000/Users?username=${this.username}`);

            console.log("[SYS-INFO]: FROM USEREXIST-STATUS: " + userExist.status);
            console.log("[SYS-INFO]: FROM USEREXIST-DATA: " + userExist.data);

            if (userExist.status === 200 && userExist.data.length > 0) {

                // DEBUG
                console.log("[SYS-INFO]: USERNAME EXIST");

                return true; // Return true if user exists

            } else {

                // DEBUG
                console.log("[SYS-INFO]: USERNAME NOT EXIST");

                return false; // Return false if user does not exist

            }
        },
        async validateAndSubmit() {

            // IF NOT ALL FILLED IN: CANNOT SUBMIT
            if (!this.username || !this.password || !this.email) {
                alert("Please fill in all fields.");
                return;
            }
            this.submitRegister();
        },

        async submitRegister() {

            if (!this.username || !this.email || !this.password) {
                alert('Please fill in all fields');
            } else {
                const userExists = await this.checkUserExist();

                // DEBUG
                console.log("[SYS-INFO]: FROM SUBMITREGISTER-BOOLEAN-DOES-USER-EXIST?: " + userExists);

                if (!userExists) {
                    let result = await axios.post('http://localhost:3000/Users', {
                        username: this.username,
                        email: this.email,
                        password: this.password
                    });

                    // DEBUG
                    console.log(result);

                    if (result.status === 201) {
                        alert('User created successfully, please proceed to Log In.');
                        //localStorage.setItem('user-info', JSON.stringify(result.data));
                        this.$router.push({ name: 'LoginPage' });
                    } else {
                        alert('Failed to create user, please try again.');
                    }
                } else {
                    alert('Username already exists, please try another username.');
                }
            }
        },

        goBack() {
            this.$router.push({ name: 'LandingPage' });
        },
        goLogin() {
            this.$router.push({ name: 'LoginPage' });
        }
    },
    mounted() {
        if (localStorage.getItem('user-info')) {
            this.$router.push({ name: 'DashboardPage' });
        }
    }
}
</script>


<style>
.logo {
    width: 100px;
}

.register input {
    width: 300px;
    height: 40px;
    display: block;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
}
</style>