<template>
    <div>
        <div v-if="memberInfo">
            <h2>Member Information</h2>
            <p><strong>Chinese Name:</strong> {{ memberInfo.chineseName }}</p>
            <p><strong>English Name:</strong> {{ memberInfo.englishName }}</p>
            <p><strong>Birth Date:</strong> {{ memberInfo.dob }}</p>
            <p><strong>HKID:</strong> {{ memberInfo.hkid }}</p>
            <p><strong>Phone Number:</strong> {{ memberInfo.telDaily }}</p>
            <p><strong>Email:</strong> {{ memberInfo.email }}</p>
            <p><strong>Address:</strong> {{ memberInfo.address }}</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'MemberInfoPage',

    data() {
        return {
            memberData: null,
        };
    },

    computed: {
        memberInfo() {
            return this.memberData && this.memberData.length > 0 ? this.memberData[0] : null;
        },
    },

    methods: {
        async getMemberInfo() {
            try {
                const memberID = this.$route.query.memberID;

                const response = await axios.get(`http://localhost:3000/Person?memberID=${memberID}`);
                this.memberData = response.data;
            } catch (error) {
                console.error('Error fetching member info:', error);
            }
        },
    },

    mounted() {
        this.getMemberInfo();

        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }

    },
};
</script>