<template>

    <h1>Create Treatment</h1>

    <div class="create-treatment">
        <div class="row">
            <label for="treatmentName">Treatment Name:</label>
            <input type="text" v-model="treatmentName" placeholder="Treatment Name: ">

            <label for="treatmentID">Treatment ID:</label>
            <input type="text" v-model="treatmentID" placeholder="Treatment ID: ">
        </div>

        <div class="row">
            <label for="treatmentType">Treatment Type:</label>
            <input type="text" v-model="treatmentType" placeholder="Type of Treatment: ">

            <label for="treatmentFee">Treatment Fee:</label>
            <input type="text" v-model="treatmentFee" placeholder="Fee: ">
        </div>

        <div class="row">
            <label for="remark">Remark:</label>
            <textarea v-model="remark" name="remark" rows="10" cols="100" placeholder="Remarks: ">
            </textarea>

        </div>

    </div>

    <button class="button-56" v-on:click="validateAndSubmit">Submit</button>

</template>

<script>

import axios from 'axios';

export default {

    name: 'CreateTreatmentPage',

    data() {
        return {
            treatmentName: '',
            treatmentID: '',
            treatmentType: '',
            treatmentFee: '',
        }
    },

    methods: {
        async validateAndSubmit() {
            if (this.treatmentName === '' || this.treatmentID === '' || this.treatmentType === '' || this.treatmentFee === '') {
                alert('Please fill in all fields');
                return;
            }
            this.submitCreate();
        },
        async submitCreate() {

            let result = await axios.post('http://localhost:3000/Treatment', {
                treatmentName: this.treatmentName,
                treatmentID: this.treatmentID,
                treatmentType: this.treatmentType,
                treatmentFee: this.treatmentFee,
            });


            console.log(result);

            if (result.status === 201) {

                alert('Treatment created successfully!');
                this.$router.push({ name: 'TreatmentListPage' });
            }
        }
    },
    mounted() {
        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }
    }
}
</script>


