<template>
    <div>
        <div v-if="activityInfo">
            <h2>Activity Information</h2>
            <p><strong>Product Name:</strong> {{ activityInfo.productName }}</p>
            <p><strong>Product ID:</strong> {{ activityInfo.productID }}</p>
            <p><strong>Date:</strong> {{ activityInfo.date }}</p>
            <p><strong>Quota:</strong> {{ activityInfo.quota }}</p>
            <p><strong>Fee:</strong> {{ activityInfo.fee }}</p>
            <p><strong>Location:</strong> {{ activityInfo.location }}</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'ActivityInfoPage',

    data() {
        return {
            activityData: null,
        };
    },

    computed: {
        activityInfo() {
            return this.activityData && this.activityData.length > 0 ? this.activityData[0] : null;
        },
    },

    methods: {
        async getActivityInfo() {
            try {
                const productID = this.$route.query.productID;

                const response = await axios.get(`http://localhost:3000/Activity?productID=${productID}`);
                this.activityData = response.data;
            } catch (error) {
                console.error('Error fetching activity info:', error);
            }
        },
    },

    mounted() {
        this.getActivityInfo();

        if (!localStorage.getItem('user-info')) {
            this.$router.push({ name: 'LandingPage' });
        }

    },
};
</script>